	module.exports = {
		"data": {
			"site": {
				"siteMetadata": {
					"title": "Software Development Notes",
					"author": "Ryan Lanciaux"
				}
			},
			"markdownRemark": {
				"id": "/Users/ryan/projects/blog/ryanlanciaux.github.com/src/pages/2017-08-20-a-feature-based-approach-to-react-development/index.md absPath of file >>> MarkdownRemark",
				"html": "<p>There are a lot of opinions around how to arrange a React project. In this post, I want to talk about feature folders and why I think they are a great choice for React application structure.</p>\n<h2>Focus on the goal rather than the technology</h2>\n<p>In many languages and frameworks, the prevailing wisdom is to group like technologies together. For example, when starting a new Rails or .NET MVC project, we generally have a series of folders for Models, Views, Controllers, (sometimes services and other things as well). While we don’t often have these types of items in React — it is fairly common to see a Components directory, some sort of Utils directory, and if we’re using Redux, a folder for Reducers, Actions, Selectors, and so on. This works great when a project is smaller but over time, this can be unwieldy — especially when working with teams of people.</p>\n<p>Feature folders help eliminate some of these problems by placing everything that is unique to one area of the code together. For instance, if we were making a login feature, we would have a folder for Login and in this folder we would have our login components, actionCreators, reducers, selectors, etc. </p>\n<div class=\"gatsby-highlight\">\n      <pre class=\"language-none\"><code>src\n|_ features/\n|___ cart/\n|_____ components/\n|_____ actionCreators.js\n|_____ index.js\n|_____ reducer.js\n|_____ selectors.js\n|___ product/\n|___ other/</code></pre>\n      </div>\n<p>This arranges our code by what the code’s mission is and how it contributes to the overall goal of the project rather than the technology. </p>\n<h2>This is not much of a mental leap for React developers</h2>\n<p>In React, we are used to structuring our application as a series of components. Where traditional applications are built with a number of HTML files, CSS files, and JavaScript files, a component can encapsulate all of these things.</p>\n<div>\n<p>\n  <span\n    class=\"gatsby-resp-image-wrapper\"\n    style=\"position: relative; z-index: -1; display: block; \"\n  >\n    <span\n      class=\"gatsby-resp-image-background-image\"\n      style=\"padding-bottom: 67.24137931034483%;position: relative; width: 100%; bottom: 0; left: 0; background-image: url('data:image/png;base64,'); background-size: cover; display: block;\"\n    >\n      <img\n        class=\"gatsby-resp-image-image\"\n        style=\"width: 100%; margin: 0; vertical-align: middle; position: absolute; top: 0; left: 0; box-shadow: inset 0px 0px 0px 400px white;\"\n        alt=\"Perfect slide\"\n        title=\"\"\n        src=\"/static/062af7e6eb9aa650e5f0b64387f82527-a408b.png\"\n        srcset=\"/static/062af7e6eb9aa650e5f0b64387f82527-4eabf.png 148w,\n/static/062af7e6eb9aa650e5f0b64387f82527-5a375.png 295w,\n/static/062af7e6eb9aa650e5f0b64387f82527-a408b.png 590w,\n/static/062af7e6eb9aa650e5f0b64387f82527-9a0cc.png 885w,\n/static/062af7e6eb9aa650e5f0b64387f82527-3aa7a.png 928w\"\n        sizes=\"(max-width: 590px) 100vw, 590px\"\n      />\n    </span>\n  </span>\n  </p>\n</div>\n<blockquote class=\"twitter-tweet\" data-lang=\"en\"><p lang=\"en\" dir=\"ltr\">A slide so perfect I wish I made it :)<br>From <a href=\"https://t.co/VXvUNsh3wb\">https://t.co/VXvUNsh3wb</a> by <a href=\"https://twitter.com/areaweb\">@areaweb</a> <a href=\"https://t.co/TQKvI5SGpT\">pic.twitter.com/TQKvI5SGpT</a></p>&mdash; Michele Bertoli (@MicheleBertoli) <a href=\"https://twitter.com/MicheleBertoli/status/868078729662279680\">May 26, 2017</a></blockquote>\n<script async src=\"//platform.twitter.com/widgets.js\" charset=\"utf-8\"></script>\n<p>What’s interesting about this vision of separation of concerns is that it also reminiscent of the “Vertical Slices” terminology that is commonly used in some agile development circles (and also used as an argument for using feature folders). Instead of thinking of a system as a series of Actions, Reducers, Selectors, Components, an application can be imagined as a series of features. Features, at this point, can almost be seen as “components” of application structure.</p>\n<p>\n  <span\n    class=\"gatsby-resp-image-wrapper\"\n    style=\"position: relative; z-index: -1; display: block; \"\n  >\n    <span\n      class=\"gatsby-resp-image-background-image\"\n      style=\"padding-bottom: 68.08510638297872%;position: relative; width: 100%; bottom: 0; left: 0; background-image: url('data:image/png;base64,'); background-size: cover; display: block;\"\n    >\n      <img\n        class=\"gatsby-resp-image-image\"\n        style=\"width: 100%; margin: 0; vertical-align: middle; position: absolute; top: 0; left: 0; box-shadow: inset 0px 0px 0px 400px white;\"\n        alt=\"Vertical slices in a React application\"\n        title=\"\"\n        src=\"/static/d8418980acc11f9350402cf2e47a8b41-a408b.png\"\n        srcset=\"/static/d8418980acc11f9350402cf2e47a8b41-4eabf.png 148w,\n/static/d8418980acc11f9350402cf2e47a8b41-5a375.png 295w,\n/static/d8418980acc11f9350402cf2e47a8b41-a408b.png 590w,\n/static/d8418980acc11f9350402cf2e47a8b41-97700.png 611w\"\n        sizes=\"(max-width: 590px) 100vw, 590px\"\n      />\n    </span>\n  </span>\n  </p>\n<h2>What about shared code?</h2>\n<p>Shared code can (and should) still exist in a feature based paradigm. There are some things that benefit many features and those could live in a “shared” directory.</p>\n<h2>A non-exhaustive example</h2>\n<p>To illustrate how we could migrate an application to this strategy, lets compare the <a href=\"https://github.com/reactjs/redux/tree/master/examples/shopping-cart\">Redux Shopping Cart</a> to a <a href=\"https://github.com/ryanlanciaux/react_feature_folder_example\">more feature-oriented example I’ve created</a>. </p>\n<p>The initial example exists as a series of items arranged by technology (e.g. components / containers / actionCreators, etc.)</p>\n<p>\n  <span\n    class=\"gatsby-resp-image-wrapper\"\n    style=\"position: relative; z-index: -1; display: block; \"\n  >\n    <span\n      class=\"gatsby-resp-image-background-image\"\n      style=\"padding-bottom: 28.510638297872337%;position: relative; width: 100%; bottom: 0; left: 0; background-image: url('data:image/png;base64,'); background-size: cover; display: block;\"\n    >\n      <img\n        class=\"gatsby-resp-image-image\"\n        style=\"width: 100%; margin: 0; vertical-align: middle; position: absolute; top: 0; left: 0; box-shadow: inset 0px 0px 0px 400px white;\"\n        alt=\"Redux Shopping cart example\"\n        title=\"\"\n        src=\"/static/194010a28ecab8e7cf34ce86a3961c65-a408b.png\"\n        srcset=\"/static/194010a28ecab8e7cf34ce86a3961c65-4eabf.png 148w,\n/static/194010a28ecab8e7cf34ce86a3961c65-5a375.png 295w,\n/static/194010a28ecab8e7cf34ce86a3961c65-a408b.png 590w,\n/static/194010a28ecab8e7cf34ce86a3961c65-9a0cc.png 885w,\n/static/194010a28ecab8e7cf34ce86a3961c65-aa987.png 1180w,\n/static/194010a28ecab8e7cf34ce86a3961c65-2099a.png 1410w\"\n        sizes=\"(max-width: 590px) 100vw, 590px\"\n      />\n    </span>\n  </span>\n  </p>\n<p>To migrate to a more feature-oriented method of structuring our code (there are some files like tests and other things like that I leave out of this guide):</p>\n<ol>\n<li>We’ll start by creating a folder named <code>features</code> with a <code>cart</code> and <code>product</code> feature as sub folders.</li>\n<li>Create <code>components</code> directories in the <code>products</code> and <code>cart</code> folders and move the <code>components</code> and <code>container</code> components into their respective directories. Product components go under <code>features/product/components</code> where Cart components go under <code>features/cart/components</code></li>\n<li>Move <code>reducers/cart.js</code> to <code>features/cart/reducer.js</code> and <code>reducers/product.js</code> to <code>features/product/reducer.js</code>. This could still be called cartReducer if desired, however, I generally prefer to name boilerplate items in a very generic manner (while still keeping components named very specific to their function). </li>\n<li>From here create a directory called <code>shared</code> and move <code>reducers/index</code> to <code>shared/reducer.js</code></li>\n<li>At this point, you can move the selectors from <code>shared/reducer.js</code> to their own file under their respective directory (view <a href=\"https://github.com/ryanlanciaux/react_feature_folder_example/blob/master/src/features/cart/selectors.js\">features/cart/selectors.js</a> and <a href=\"https://github.com/ryanlanciaux/react_feature_folder_example/blob/master/src/features/product/selectors.js\">features/product/selectors.js</a>. I would generally recommend creating a <code>shared/selectors.js</code> file for containing selectors that are a combination of items that span more than one feature (<a href=\"https://github.com/ryanlanciaux/react_feature_folder_example/blob/master/src/shared/selectors.js\">see an example of this here</a>).</li>\n<li>Move the methods from <code>/actions/index.js</code> to <code>features/cart/actionCreators.js</code> or <code>features/product/actionCreators.js</code>.</li>\n<li>Create index files in each feature that export the main component as a default component and the other items as named exports. In this shopping cart example, the container component is the default export for both <code>cart</code> and <code>product</code></li>\n</ol>\n<div class=\"gatsby-highlight\">\n      <pre class=\"language-javascript\"><code><span class=\"token keyword\">import</span> Product <span class=\"token keyword\">from</span> <span class=\"token string\">'./components/ProductsContainer'</span><span class=\"token punctuation\">;</span>\n\n<span class=\"token keyword\">import</span> <span class=\"token operator\">*</span> <span class=\"token keyword\">as</span> actionCreators <span class=\"token keyword\">from</span> <span class=\"token string\">'./actionCreators'</span><span class=\"token punctuation\">;</span>\n<span class=\"token keyword\">import</span> <span class=\"token operator\">*</span> <span class=\"token keyword\">as</span> selectors <span class=\"token keyword\">from</span> <span class=\"token string\">'./selectors'</span><span class=\"token punctuation\">;</span>\n<span class=\"token keyword\">import</span> reducer <span class=\"token keyword\">from</span> <span class=\"token string\">'./reducer'</span><span class=\"token punctuation\">;</span>\n\n<span class=\"token keyword\">export</span> <span class=\"token punctuation\">{</span> actionCreators<span class=\"token punctuation\">,</span> selectors<span class=\"token punctuation\">,</span> reducer <span class=\"token punctuation\">}</span><span class=\"token punctuation\">;</span>\n\n<span class=\"token keyword\">export</span> <span class=\"token keyword\">default</span> Product<span class=\"token punctuation\">;</span>\n</code></pre>\n      </div>\n<br />\n<p>Once these steps are complete — we can update any references to point to their new locations. <strong>One thing of important note</strong>: it’s generally a good idea to reference any part of a feature through the feature’s <code>index</code> rather than the file directly. This treats the index as each feature’s API contract and quickly illuminates any issues that may occur with any refactoring.</p>\n<p />\n<p><a href=\"https://github.com/ryanlanciaux/react_feature_folder_example\">View the example on GitHub</a></p>\n<h2>Benefits of this model</h2>\n<h4>Code is structured in a way that reflects purpose</h4>\n<p>This makes it easier to find files and know where things belong. Additionally, it helps clarify how a bit of code contributes to the overall goals of an application.</p>\n<h4>Code is easier to refactor</h4>\n<p>If a feature has one entry point, it’s clear that removing the feature will have limited impact on the rest of the system. Additionally, if changes are made to a login feature, they should have no impact on a “cart” feature.</p>\n<h4>Imagine applications a series of smaller applications</h4>\n<p>I like thinking of features as small applications that work together to a greater goal — kind of like the <a href=\"https://en.wikipedia.org/wiki/Unix_philosophy\">Unix Philosophy</a> “Write <em>features</em> that do one thing and do it well. Write <em>features</em> to work together.”</p>\n<h4>Feature Flags</h4>\n<p><small>(feature is kind of an overloaded term here)</small></p>\n<p><a href=\"https://stackoverflow.com/a/11164444/1385358\">“Feature Flag is a technique to turn some functionality of your application off, via configuration, without deploying new code.”</a>. While not entirely related, organizing an application as a series of features can make feature flags easier to implement. </p>\n<p>If we have a feature, buyNow structured as follows</p>\n<div class=\"gatsby-highlight\">\n      <pre class=\"language-none\"><code>...\n|_ src/\n|___ features/\n|_____ buyNow/\n|_______ components/\n|_________ BuyNowButton.js\n|_______ index.js\n|_____ otherFeature</code></pre>\n      </div>\n<p>And our index for our buyNow feature looks like this</p>\n<div class=\"gatsby-highlight\">\n      <pre class=\"language-javascript\"><code><span class=\"token keyword\">import</span> BuyNowButton <span class=\"token keyword\">from</span> <span class=\"token string\">'./components/BuyNowButton'</span><span class=\"token punctuation\">;</span>\n\n<span class=\"token keyword\">export</span> <span class=\"token keyword\">default</span> BuyNowButton<span class=\"token punctuation\">;</span>\n</code></pre>\n      </div>\n<p>We could have a simple feature / featureCheck for turning parts of our application on and off</p>\n<div class=\"gatsby-highlight\">\n      <pre class=\"language-javascript\"><code><span class=\"token comment\" spellcheck=\"true\">// overly simplistic for sake of example</span>\n<span class=\"token keyword\">const</span> features <span class=\"token operator\">=</span> <span class=\"token punctuation\">{</span>\n  <span class=\"token operator\">...</span>\n  buyNow<span class=\"token punctuation\">:</span> <span class=\"token boolean\">false</span>\n<span class=\"token punctuation\">}</span>\n\n<span class=\"token comment\" spellcheck=\"true\">// overly simplistic for sake of example</span>\n<span class=\"token keyword\">export</span> <span class=\"token keyword\">function</span> <span class=\"token function\">isFeatureEnabled</span><span class=\"token punctuation\">(</span>feature<span class=\"token punctuation\">)</span> <span class=\"token punctuation\">{</span>\n  <span class=\"token keyword\">return</span> features<span class=\"token punctuation\">[</span>feature<span class=\"token punctuation\">]</span> <span class=\"token operator\">||</span> <span class=\"token boolean\">false</span><span class=\"token punctuation\">;</span>\n<span class=\"token punctuation\">}</span>\n</code></pre>\n      </div>\n<p>We could adjust our index to return undefined instead of the component if the feature was disabled</p>\n<div class=\"gatsby-highlight\">\n      <pre class=\"language-javascript\"><code><span class=\"token keyword\">import</span> <span class=\"token punctuation\">{</span> isFeatureEnabled <span class=\"token punctuation\">}</span> <span class=\"token keyword\">from</span> <span class=\"token string\">'../shared/features'</span><span class=\"token punctuation\">;</span>\n<span class=\"token keyword\">import</span> BuyNowButton <span class=\"token keyword\">from</span> <span class=\"token string\">'./components/BuyNowButton'</span><span class=\"token punctuation\">;</span>\n\n<span class=\"token comment\" spellcheck=\"true\">// ideally we would create something a bit more sophisticated for handling feature flags</span>\n<span class=\"token keyword\">export</span> <span class=\"token keyword\">default</span> <span class=\"token function\">isFeatureEnabled</span><span class=\"token punctuation\">(</span><span class=\"token string\">'buyNow'</span><span class=\"token punctuation\">)</span> <span class=\"token operator\">?</span> BuyNowButton <span class=\"token punctuation\">:</span> undefined<span class=\"token punctuation\">;</span>\n</code></pre>\n      </div>\n<p>When the <code>buyNow</code> feature is set to false in the featureFlag file, the component will not render.</p>\n<h2>Wrapping up</h2>\n<ul>\n<li>Structure application by features</li>\n<li>Feature is a vertical slice through application layers</li>\n<li>There should be some value when feature is done</li>\n<li>Organizing files by feature allows for quicker deletion/refactoring</li>\n</ul>\n<p><a href=\"https://github.com/ryanlanciaux/react_feature_folder_example\">Feature example available here</a></p>\n<h2>Additional Resources</h2>\n<ul>\n<li><a href=\"https://speakerdeck.com/didoo/let-there-be-peace-on-css\">Let There Be Peace On CSS by Cristiano Rastelli</a></li>\n<li><a href=\"https://msdn.microsoft.com/en-us/magazine/mt763233.aspx\">Feature slices in .NET Core</a></li>\n<li><a href=\"https://vimeo.com/131633177\">SOLID Architecture in Slices not Layers - Jimmy Bogard</a></li>\n</ul>",
				"frontmatter": {
					"title": "A feature based approach to React development",
					"date": "August 24, 2017"
				}
			}
		},
		"pathContext": {
			"path": "/blog/2017/08/20/a-feature-based-approach-to-react-development/"
		}
	};

